<template>
  <div class="users">
    <app-table
      title="Usuários"
      :currentPage="currentPage"
      :headers="headers"
      :items="itemsModel"
      :loading="loading"
      :qtdPages="qtdPages"
      @onActionClicked="callAction"
      @onChangeOrder="changeOrder"
      @onChangeStatus="changeStatus"
      @onPageChanged="changePage"
      @onSearch="searchValue"
    />
  </div>
</template>

<script>
import AppTable from '@/components/moderators/Table';
import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';

export default {
  name: 'Users',
  components: { AppTable },
  mixins: [ DateMixin, SnackbarMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '#', value: 'id', align: 'center', width: '60' },
      { text: 'Imagem', value: 'barberImage', align: 'center', width: '80' },
      { text: 'Nome', value: 'name', align: 'start' },
      { text: 'E-mail', value: 'email', align: 'start' },
      { text: 'Cadastro', value: 'data', align: 'start' },
      { text: 'Status', value: 'enabled', align: 'start' },
      { text: 'Açoes', value: 'actions', align: 'center', width: '60' },
    ],
    items: [],
    loading: true,
    order: null,
    qtdPages: 1,
    search: null,
    status: null
  }),
  computed: {
    itemsModel () {
      return this.items.map((user) => {
        const action = [
          {
            icon: user.enabled ? 'lock_open' : 'lock',
            class: user.enabled ? 'blue' : 'gray3',
            action: user.enabled ? 'block' : 'unblock',
            text: user.enabled ? 'Bloquear' : 'Desbloquear',
          },
        ];
        return {
          id: user.id,
          name: user.name,
          email: user.email,
          barberImage: user.image_url,
          enabled: user.enabled,
          data: this.formatDateTimeToStringPtBr(new Date(user.created_at)),
          actions: action,
        };
      });
    }
  },
  mounted () {
    this.getUsers();
  },
  methods: {
    blockUser(user) {
      const errorMessage = 'Não foi possível bloquear o usuário!'
      this.$axios.put(`${this.$url_api}/user/${user.id}/block`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(u => Number(user.id) === Number(u.id))
            if (index > -1) {
              let item = this.items[index]
              item.enabled = false
              this.items.splice(index, 1, item)
            }
            this.showSnackbar(response.data.corpo)
          } else this.setErro(errorMessage)
        } else this.setErro(errorMessage)
      }).catch(() => this.setErro(errorMessage))
    },
    callAction(action, item) {
      switch (action) {
        case 'block':
          this.blockUser(item);
          break;
        case 'unblock':
          this.unblockUser(item);
          break;
      }
    },
    changePage(page) {
      this.currentPage = page;
      this.getUsers();
    },
    changeOrder (order) {
      this.order = order
      this.getUsers()
    },
    changeStatus (status) {
      this.status = status
      this.getUsers()
    },
    getUsers () {
      const message = 'Não foi possível buscar os usuários!'
      this.loading = true;
      let url = `${this.$url_api}/user`;
      let operator = '?';
      if (this.search) {
        url += `${operator}search=${this.search}`;
        operator = '&';
      }
      if (this.order) {
        url += `${operator}order=${this.order}`;
        operator = '&';
      }
      if (this.status) url += `${operator}status=${this.status}`;
      
      if (this.currentPage) url += `${operator}page=${this.currentPage}`;
      this.$axios(url)
        .then((response) => {
          this.loading = false;
          if (response.status === 200) {
            if (response.data.codigo === 200) {
              this.currentPage = response.data.corpo.current_page;
              this.qtdPages = response.data.corpo.last_page;
              this.items = response.data.corpo.data
            } else this.setError(response.data.corpo);
          } else this.setError(message);
        })
        .catch(() => this.setError(message));
    },
    searchValue(val) {
      this.currentPage = null;
      this.search = val;
      this.getUsers();
    },
    setError(message) {
      this.loading = false;
      this.showSnackbarErro(message);
    },
    unblockUser(user) {
      const errorMessage = 'Não foi possível desbloquear o usuário!'
      this.$axios.put(`${this.$url_api}/user/${user.id}/unblock`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(u => user.id === u.id)
            if (index > -1) {
              let item = this.items[index]
              item.enabled = true
              this.items.splice(index, 1, item)
            }
            this.showSnackbar(response.data.corpo)
          } else this.setErro(errorMessage)
        } else this.setErro(errorMessage)
      }).catch(() => this.setErro(errorMessage))
    }
  }
}
</script>