<template>
  <card class="mt-4 mt-md-0">
    <p>
      <span class="font-weight-bold">
        {{ title }}
      </span>
    </p>
    <v-row>
      <v-col v-if="showOrder" cols="12" sm="6" md="auto">
        <select-field
          label="Ordenar por:"
          class="table-select"
          :outlined="true"
          :dense="true"
          :value="order"
          :items="orderItems"
          @onChange="changeOrder"
        />
      </v-col>
      <v-col v-if="showFilter" cols="12" sm="6" md="auto">
        <select-field
          label="Status"
          class="table-select"
          :value="status"
          :items="statusItems"
          :outlined="true" 
          :dense="true"
           @onChange="changeStatus"
        />
      </v-col>
      <v-spacer></v-spacer>
      <v-col v-if="showSearch" cols="12" md="auto">
        <input-field
          label="Pesquisar"
          class="table-input"
          :outlined="true"
          :success="false"
          :dense="true"
          icon="search"
          @onEnter="searchValue"
        />
      </v-col>
    </v-row>
    <v-data-table
      class="moderators-table"
      :fixed-header="true"
      :headers="headers"
      :items="items"
      :disable-filtering="true"
      :disable-pagination="true"
      :disable-sort="true"
      :loading="loading"
      :hide-default-footer="true"
      loading-text="Carregando"
      locale="pt-Br"
      no-data-text="Nenhum registro encontrado"
      no-results-text="Nenhum registro encontrado"
    >
      <template v-slot:item.barberImage="{ item }">
        <div class="image">
          <img
            v-if="item.barberImage"
            :style="`background-image: url(${$url_backend + item.barberImage})`" 
          />
        </div>
      </template>

      <template v-slot:item.enabled="{ item }">
        <v-chip
          class="barberStatus"
          :class="item.enabled ? 'success' : 'gray2'"
          light
          small
        >
          {{ item.enabled ? 'Ativo' : 'Bloqueado' }}
        </v-chip>
      </template>

      <template v-slot:item.barbershopStatus="{ item }">
        <v-chip
          :class="'barbershop-status-' + item.barbershopStatus.id"
          light
          small
        >
          {{ item.barbershopStatus.name }}
        </v-chip>
      </template>
      
      <template v-slot:item.actions="{ item }">
        <btn-icon
           v-if="false" 
          :icon="item.actions[0].icon"
          :small="true"
          :class="item.actions[0].class"
          class="btn-action d-none d-sm-inline"
          @clicked="callAction(item.actions[0].action, item)"
        />
        <v-btn
           v-if="item.actions.length > 0"
          :class="item.actions[0].class"
          class="btn-action text-none primary--text"
          @click="callAction(item.actions[0].action, item)"
        >
         <v-icon class="mr-1" small>{{ item.actions[0].icon }}</v-icon>  {{ item.actions[0].text }}
        </v-btn>
      </template>
    </v-data-table>

    <div class="text-center my-4">
    <v-pagination
      v-model="page"
      :length="qtdPages"
      :total-visible="8"
      color="primary"
      @input="changePage"
    ></v-pagination>
  </div>
  </card>
</template>

<script>
import BtnIcon from '@/components/globais/buttons/icon/BtnIcon'
import Card from '@/components/globais/Card'
import InputField from '@/components/globais/form/input-field/InputField'
import SelectField from '@/components/globais/form/select-field/SelectField'

export default {
  name: 'Table',
  components: { BtnIcon, Card, InputField, SelectField },
  props: {
    currentPage: { default: 1 },
    headers: { default: Array },
    items: { items: Array },
    loading: { default: false },
    qtdPages: { default: 1 },
    title: { default: null },
    statusItems: {
      type: Array, 
      default () {
        return [
          { text: 'Todos', value: null },
          { text: 'Ativos', value: 1 },
          { text: 'Bloqueados', value: 0 }
        ]
      }
    },
    showOrder: { deafult: false },
    showFilter: { deafult: false },
    showSearch: { deafult: false },
  },
  data: () => ({
    page: 1,
    status: null,
    order: null,
    orderItems: [
      { text: 'Cadastro', value: null },
      { text: 'Nome', value: 'name' }
    ]
  }),
  watch: {
    currentPage (val) {
      this.page = val
    }
  },
  mounted () {
    this.page = this.currentPage
  },
  methods: {
    callAction(action, item) {
      this.$emit('onActionClicked', action, item)
    },
    changeOrder (val) {
      this.$emit('onChangeOrder', val)
    },
    changePage (page) {
      this.$emit('onPageChanged', page)
    },
    changeStatus (status) {
      this.$emit('onChangeStatus', status)
    },
    searchValue (val) {
      this.$emit('onSearch', val)
    }
  }
}
</script>

