  table { min-width: 900px; }
  .image {
    width: 60px;
    height: 60px;
    background: #ccc;
    border-radius: 50%;
    margin: auto;
    position: relative;
    overflow: hidden;
    img { width: 100%; height: 100%; display: block; background-size: contain; }
  }
  td { padding: 10px 16px; word-break: break-word; }
  .barberStatus { color: #000 !important; }
  .v-pagination__item { border: none !important; }
  .barbershop-status-2 { background: $success !important; }
  .barbershop-status-3 { background: $danger !important; }
  .btn-action { border-radius: 15px; font-size: .8rem; }
}
@media all and (max-width: 599px) {
  .moderators-table {
    table { min-width: 100%; }
    td { padding: 0; }
    tr { padding: 10px 0; }
    .v-data-table__mobile-row__header { word-break: keep-all; }
  }
}
</style>

<style scoped>
.table-select { width: 200px; }
@media all and (max-width: 959px) {
  .table-select { width: 100%; }
}
@media all and (max-width: 599px) {
  .btn-action { border-radius: 10px; margin: 10px 0 20px; }
}
</style>